/* Указываем box sizing */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Убираем внутренние отступы  */
ul[class],
ol[class] {
    padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
    margin: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
    min-height: 100vh;
    scroll-behavior: smooth;
    text-rendering: optimizeSpeed;
    line-height: 1.5;
    font-family: Fiorina, sans-serif;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
    list-style: none;
}

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a:not([class]) {
    text-decoration-skip-ink: auto;
}

/* Упрощаем работу с изображениями */
img {
    max-width: 100%;
    display: block;
}

/* Указываем понятную периодичность в потоке данных у article*/
article > * + * {
    margin-top: 1em;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
    font: inherit;
}

/* Удаляем все анимации и переходы для людей, которые предпочитай их не использовать */
@media (prefers-reduced-motion: reduce) {
    * {
        animation-duration: 0.01ms !important;
        animation-iteration-count: 1 !important;
        transition-duration: 0.01ms !important;
        scroll-behavior: auto !important;
    }
}

@font-face {
    font-family: Fiorina;
    src: local("Fiorina"), url("./fonts/FiorinaText-Light.ttf") format("truetype");
    font-weight: 300;
}

@font-face {
    font-family: Fiorina;
    src: local("Fiorina"), url("./fonts/FiorinaTitle-Light.ttf") format("truetype");
    font-weight: normal;
}

@font-face {
    font-family: Fiorina;
    src: local("Fiorina"), url("./fonts/FiorinaSubhead-Light.ttf") format("truetype");
    font-weight: 500;
}

@font-face {
    font-family: Fiorina;
    src: local("Fiorina"), url("./fonts/FiorinaGrande-Light.ttf") format("truetype");
    font-weight: bold;
}

:root {
    --red-color: #FF0000;
    --dim-red-color: #FF000070;
    --white-color: white;
    --light-grey-color: rgba(255, 255, 255, 0.25);
    --light-white-color: rgba(255, 255, 255, 0.7);
    --medium-black: rgba(0, 0, 0, 0.75);
    --bold-black: rgba(0, 0, 0, 0.9);
}
